<template>
<ExampleGetSpain :my-list="list">

</ExampleGetSpain>
</template>

<script>
import {onMounted, reactive} from "vue";
import EndPoints from "@/service/EndPoints";
import{defineAsyncComponent} from "vue";
const ExampleGetSpain=defineAsyncComponent(()=>import('@/components/main/ExampleGetSpain'))
import {handleError} from "@/components/composable/handleError";
export default {
  name: "AllAwayStanding",
  components: {ExampleGetSpain},
  setup(){
    const list=reactive([])
    const result=onMounted(()=>{
      EndPoints.getGoalsGermany()
          .then((response)=>{
            if(response.data.success){
              list.push(response.data.data.standing.standings_away)
            }
          })
          .catch((error)=>{
            handleError(error)
          })

    })
    return{
      list,
      result,
    }
  }
}
</script>

